import {reactive,toRefs,onMounted,defineComponent} from 'vue';
import {useRouter} from 'vue-router';
import {getRequest} from '../../request/api';
import url from '../../request/url';
import BgBox from '../../components/BgBox'

export default defineComponent({
    components:{
        BgBox
    },
    setup () {
        let state = reactive({
            chosenAddressId: "",
            list: []
        })

        let {back,getAddressData,add,edit} = methodes(state);

        onMounted(() => {
            getAddressData();
        })

        return {
            ...toRefs(state),
            back,
            add,
            edit
        }
    }
})

function methodes(state){
    let router = useRouter();

    let back = () => {
        router.back();
    }

    let getAddressData = () => {
        getRequest(url.findAddress).then(({code,result}) => {
            if(code == 20000){
                result.map(item => {
                    Reflect.set(item,'isDefault',Boolean(item.isDefault));
                    Reflect.set(item,'address',`${item.province}${item.city}${item.county}${item.addressDetail}`);
                });
                Reflect.set(state,'list',result);
            }
        }).catch((err) => {
            console.log("err==>",err);
        });
    }

    let add = () => {
        router.push({ name: "newAddress" });
    }

    let edit = ({aid}) => {
        router.push({ name: "newAddress", query: { aid } });
    }


    return{
        back,
        getAddressData,
        add,
        edit

    }

}